.card2 {
   color: #000000;
   align-items: center;
   margin: 2rem 2rem 0rem 2rem;
   border-radius: 1.5rem;
   background-color: rgb(255, 255, 255);
   display: flex;
   justify-content: space-between !important;
}

.container-data {
   margin-left: auto;
}

.container-data h3 {
   font-size: 1.5rem;
   font-weight: 500;
}
.differnce-arrow {
   background-color: #c3f9d9;
   border-radius: 1.5rem;
   font-size: 0.7rem;
   color: #45ca6b;
   padding: 0 0.4rem 0 0.4rem;
   text-align: center;
   margin-left: 1rem;
}

.differnce-with-val {
   margin-top: 1.5rem;
   display: flex;
   align-items: center;
}

.text-opacity {
   opacity: 50%;
   font-size: 0.8rem;
}

@media (max-width: 952px) {
   .transaction-main {
      padding: 0.5rem;
   }
   .card2 {
      margin: 0.3rem;
      margin-top: 1rem;
   }
}
